<template>
  <div class="page-container" v-if="!pageLoading">
    <base-breadcrumbs :parents="[['Home','/']]" :currentLocation="'Klanten'"/>
    <div class="page-box accent">
      <div class="page-box-title">
        <h2>
          <base-icon iconRef="people" position="left"/>
          <span>Klanten</span>
        </h2>
        <div class="flex-divider"></div>
        <base-button @click="openCreateModal">
          <base-icon iconRef="plus" position="left"/>Nieuwe Klant
        </base-button>
      </div>
      <div class="page-box-datatable-container">
        <clients-data-list :dataList="clients" @click="dataEntryClick($event)"/>
      </div>
    </div>
  </div>
</template>

<script>
import clientsDataList from "./data-list-components/clients-data-list.vue";

export default {
  name: "",
  components: {
    clientsDataList
  },
  data: function() {
    return {};
  },
  computed: {
    pageLoading: function() {
      return this.$store.state.loading.page;
    },
    clients: function() {
      return this.$store.getters.getClients;
    }
  },
  methods: {
    openCreateModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "client",
        modalType: "create",
        shown: true
      });
    },
    dataEntryClick(value) {
      this.openClient(value);
    },
    openClient(targetClientId) {
      this.$router.push(`/klant/${targetClientId}`);
    }
  },
  created: function() {
    this.$store.dispatch("setPageData", {
      type: "clients"
    });
  }
};
</script>

<style>
</style>



