import Vue from "vue";
import Vuex from "vuex";
import Firebase from "firebase";

Vue.use(Vuex);

// Firebase configuration
const firebaseConfig = {
  apiKey: "AIzaSyA6X7Kxa7dajDykCMGU4s2EMLLK5j4Agpw",
  authDomain: "sonar-2-prototype.firebaseapp.com",
  databaseURL: "https://sonar-2-prototype.firebaseio.com",
  projectId: "sonar-2-prototype",
  storageBucket: "sonar-2-prototype.appspot.com",
  messagingSenderId: "335223530722"
};

// Initialize firebase
Firebase.initializeApp(firebaseConfig);
Firebase.firestore().settings({ timestampsInSnapshots: true });

export const store = new Vuex.Store({
  state: {
    user: {},
    pageData: {
      clients: [],
      currentClient: {},
      projects: [],
      currentProject: {},
      rooms: [],
      currentRoom: {},
      measurements: [],
      currentMeasurement: {}
    },
    loading: {
      page: false
    },
    modals: {
      client: {
        create: {
          shown: false
        },
        edit: {
          shown: false
        }
      },
      project: {
        create: {
          shown: false
        },
        edit: {
          shown: false
        }
      },
      room: {
        create: {
          shown: false
        },
        edit: {
          shown: false
        }
      },
      measurement: {
        create: {
          shown: false
        },
        edit: {
          shown: false
        }
      },
      analysis: {
        view: {
          shown: false
        }
      },
      advice: {
        view: {
          shown: false
        }
      }
    }
  },
  getters: {
    getUser: state => {
      return state.user;
    },
    getClients: state => {
      return state.pageData.clients;
    },
    getCurrentClient: state => {
      return state.pageData.currentClient;
    },
    getCurrentClientProjects: state => {
      return state.pageData.projects;
    },
    getCurrentProject: state => {
      return state.pageData.currentProject;
    },
    getCurrentProjectRooms: state => {
      return state.pageData.rooms;
    },
    getCurrentRoom: state => {
      return state.pageData.currentRoom;
    },
    getCurrentRoomMeasurements: state => {
      return state.pageData.measurements;
    },
    getMeasurement: state => {
      return state.pageData.currentMeasurement;
    },
    getModalStates: state => {
      return state.modals;
    }
  },
  mutations: {
    setUser: state => {
      state.user = Firebase.auth().currentUser;
    },
    setClients: (state, clientsList) => {
      state.pageData.clients = clientsList;
    },
    setCurrentClient: (state, currentClientData) => {
      state.pageData.currentClient = currentClientData;
    },
    setProjects: (state, projectsList) => {
      state.pageData.projects = projectsList;
    },
    setCurrentProject: (state, currentProjectData) => {
      state.pageData.currentProject = currentProjectData;
    },
    setRooms: (state, roomsList) => {
      state.pageData.rooms = roomsList;
    },
    setMeasurements: (state, measurementsList) => {
      state.pageData.measurements = measurementsList;
    },
    setCurrentRoom: (state, currentRoomData) => {
      state.pageData.currentRoom = currentRoomData;
    },
    setModalShown: (state, modalData) => {
      state.modals[modalData.dataType][modalData.modalType].shown = modalData.shown;
    },
    setLoading: (state, loadingData) => {
      state.loading[loadingData.dataType] = loadingData.status;
      console.log("loading", loadingData.status);
    },
    clearPageData: state => {
      state.pageData = {
        clients: [],
        currentClient: {},
        projects: [],
        currentProject: {},
        rooms: [],
        currentRoom: {},
        measurements: [],
        currentMeasurement: {}
      };
    }
  },
  actions: {
    setUser: ({ commit }) => {
      commit("setUser");
    },
    setPageData: ({ commit, dispatch }, pageData) => {
      commit("setLoading", { dataType: "page", status: true });
      commit("clearPageData");
      if (pageData.type === "clients") {
        dispatch("fetchClientsList");
      } else if (pageData.type === "client") {
        dispatch("fetchProjects", pageData);
      } else if (pageData.type === "project") {
        dispatch("fetchRooms", pageData);
      } else if (pageData.type === "room") {
        dispatch("fetchMeasurements", pageData);
      }
    },
    // Fetching actions
    fetchClientsList: ({ commit }) => {
      Firebase.firestore()
        .collection("clients")
        .where("trashed", "==", false)
        .get()
        .then(querySnapshot => {
          const clientsList = [];
          querySnapshot.forEach(doc => {
            clientsList.push(doc);
          });
          commit("setClients", clientsList);
          commit("setLoading", { dataType: "page", status: false });
        })
        .catch(error => {
          console.log("Error fetching clients: ", error);
          commit("setLoading", { dataType: "page", status: false });
        });
    },
    fetchClientData: ({ commit }, pageData) => {
      Firebase.firestore()
        .collection("clients")
        .doc(pageData.id)
        .get()
        .then(doc => {
          commit("setCurrentClient", doc);
          commit("setLoading", { dataType: "page", status: false });
        })
        .catch(error => {
          console.log("Error fetching clientData: ", error);
        });
    },
    fetchProjects: ({ commit, dispatch }, pageData) => {
      Firebase.firestore()
        .collection("projects")
        .where("trashed", "==", false)
        .where("belongsTo", "==", pageData.id)
        .get()
        .then(querySnapshot => {
          const projectsList = [];
          querySnapshot.forEach(doc => {
            projectsList.push(doc);
          });
          dispatch("fetchClientData", pageData);
          commit("setProjects", projectsList);
        })
        .catch(error => {
          console.log("Error fetching projects: ", error);
        });
    },
    fetchProjectData: ({ commit, dispatch }, pageData) => {
      Firebase.firestore()
        .collection("projects")
        .doc(pageData.id)
        .get()
        .then(doc => {
          dispatch("fetchClientData", { id: doc.data().belongsTo });
          commit("setCurrentProject", doc);
        })
        .catch(error => {
          console.log("Error fetching projectData: ", error);
        });
    },
    fetchRooms: ({ commit, dispatch }, pageData) => {
      Firebase.firestore()
        .collection("rooms")
        .where("trashed", "==", false)
        .where("belongsTo", "==", pageData.id)
        .get()
        .then(querySnapshot => {
          const roomsList = [];
          querySnapshot.forEach(doc => {
            roomsList.push(doc);
          });
          dispatch("fetchProjectData", pageData);
          commit("setRooms", roomsList);
        })
        .catch(error => {
          console.log("Error fetching rooms: ", error);
        });
    },
    fetchRoomData: ({ commit, dispatch }, pageData) => {
      Firebase.firestore()
        .collection("rooms")
        .doc(pageData.id)
        .get()
        .then(doc => {
          dispatch("fetchProjectData", { id: doc.data().belongsTo });
          commit("setCurrentRoom", doc);
        })
        .catch(error => {
          console.log("Error fetching roomData: ", error);
        });
    },
    fetchMeasurements: ({ commit, dispatch }, pageData) => {
      Firebase.firestore()
        .collection("measurements")
        .where("trashed", "==", false)
        .where("belongsTo", "==", pageData.id)
        .get()
        .then(querySnapshot => {
          const measurementsList = [];
          querySnapshot.forEach(doc => {
            measurementsList.push(doc);
          });
          dispatch("fetchRoomData", pageData);
          commit("setMeasurements", measurementsList);
        })
        .catch(error => {
          console.log("Error fetching measurements: ", error);
        });
    },
    // Creating actions
    createClient: ({ commit, dispatch }, newClient) => {
      commit("setLoading", { dataType: "page", status: true });
      Firebase.firestore()
        .collection("clients")
        .add(newClient)
        .then(() => {
          dispatch("setPageData", { type: "clients" });
        })
        .catch(error => {
          console.log("Error creating client: ", error);
          commit("setLoading", { dataType: "page", status: false });
        });
    },
    createProject: ({ commit, dispatch }, newProject) => {
      commit("setLoading", { dataType: "page", status: true });
      Firebase.firestore()
        .collection("projects")
        .add(newProject)
        .then(() => {
          dispatch("setPageData", { type: "client", id: newProject.belongsTo });
        })
        .catch(error => {
          console.log("Error creating project: ", error);
          commit("setLoading", { dataType: "page", status: false });
        });
    },
    createRoom: ({ commit, dispatch }, newRoom) => {
      commit("setLoading", { dataType: "page", status: true });
      Firebase.firestore()
        .collection("rooms")
        .add(newRoom)
        .then(() => {
          dispatch("setPageData", { type: "project", id: newRoom.belongsTo });
        })
        .catch(error => {
          console.log("Error creating room: ", error);
          commit("setLoading", { dataType: "page", status: false });
        });
    },
    createMeasurement: ({ commit, dispatch }, newMeasurement) => {
      commit("setLoading", { dataType: "page", status: true });
      Firebase.firestore()
        .collection("measurements")
        .add(newMeasurement)
        .then(() => {
          dispatch("setPageData", { type: "room", id: newMeasurement.belongsTo });
        })
        .catch(error => {
          console.log("Error creating measurement: ", error);
          commit("setLoading", { dataType: "page", status: false });
        });
    },
    // Editing actions
    editClient: ({ commit, dispatch }, clientData) => {
      commit("setLoading", { dataType: "page", status: true });
      Firebase.firestore()
        .collection("clients")
        .doc(clientData.id)
        .set(clientData.body, { merge: true })
        .then(() => {
          if (!clientData.deleting) {
            dispatch("setPageData", { type: "client", id: clientData.id });
          }
        })
        .catch(error => {
          console.log("Error editing client: ", error);
          commit("setLoading", { dataType: "page", status: false });
        });
    },
    editProject: ({ commit, dispatch }, projectData) => {
      commit("setLoading", { dataType: "page", status: true });
      Firebase.firestore()
        .collection("projects")
        .doc(projectData.id)
        .set(projectData.body, { merge: true })
        .then(() => {
          if (!projectData.deleting) {
            dispatch("setPageData", { type: "project", id: projectData.id });
          }
        })
        .catch(error => {
          console.log("Error editing project: ", error);
          commit("setLoading", { dataType: "page", status: false });
        });
    },
    editRoom: ({ commit, dispatch }, roomData) => {
      commit("setLoading", { dataType: "page", status: true });
      Firebase.firestore()
        .collection("rooms")
        .doc(roomData.id)
        .set(roomData.body, { merge: true })
        .then(() => {
          if (!roomData.deleting) {
            dispatch("setPageData", { type: "room", id: roomData.id });
          }
        });
    }
  }
});



