<template>
  <div class="page-container" v-if="pageLoading">
    <div class="page-box-loader-container">
      <base-icon iconRef="loader" :spinning="true"/>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data: function() {
    return {};
  },
  computed: {
    pageLoading: function() {
      return this.$store.state.loading.page;
    }
  }
};
</script>


