<template>
  <div class="login-container">
    <div class="login-box">
      <div class="login-box-title">
        <h2>Log in</h2>
      </div>
      <div class="login-box-content">
        <div class="input-container">
          <label for="logInEmail" class="label">E-mail</label>
          <base-input
            id="logInEmail"
            name="email"
            type="email"
            placeholder="voornaam@rivasono.nl"
            v-model="loginCredentials.email"
            @enter="logIn"
          />
          <div class="input-warning" v-if="logInEmailError.occured">
            <span class="input-warning-text">
              <base-icon iconRef="alert-circle" position="left"/>
              {{logInEmailError.message}}
            </span>
          </div>
        </div>
        <div class="input-container">
          <label for="logInPassword" class="label">Wachtwoord</label>
          <base-input
            type="password"
            name="Password"
            id="logInPassword"
            class="input"
            placeholder="****"
            v-model="loginCredentials.password"
            @enter="logIn"
          />
          <div class="input-warning" v-if="passwordError.occured">
            <span class="input-warning-text">
              <base-icon iconRef="alert-circle" position="left"/>
              {{passwordError.message}}
            </span>
          </div>
        </div>
      </div>
      <div class="login-box-footer">
        <base-button
          id="log-in-button"
          :type="'primary'"
          :loading="loggingIn"
          :disabled="loggingIn"
          @click="logIn"
        >
          <base-icon iconRef="log-in" position="left"/>Log in
        </base-button>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import Icon from "./Icon.vue";

export default {
  name: "login",
  components: {
    Icon
  },
  data: function() {
    return {
      loginCredentials: {
        email: "",
        password: ""
      },
      logInEmailError: {
        occured: false,
        message: ""
      },
      passwordError: {
        occured: false,
        message: ""
      },
      loggingIn: false
    };
  },
  methods: {
    logIn: function() {
      this.clearErrors();
      this.loggingIn = true;
      firebase
        .auth()
        .signInWithEmailAndPassword(
          this.loginCredentials.email,
          this.loginCredentials.password
        )
        .then(() => {
          this.$store.dispatch("setUser");
          this.loggingIn = false;
          this.$router.replace("home");
        })
        .catch(err => {
          this.errorOccured(err);
          // TODO: Handle individual errors
          // - auth/invalid-email, auth/user-disabled, auth/user-not-found, auth/wrong-password
          this.loggingIn = false;
        });
    },
    errorOccured: function(err) {
      const errorCode = err.code;
      console.log(err.code);
      if (errorCode == "auth/invalid-email") {
        this.logInEmailError = {
          occured: true,
          message: "Het email adres is niet goed geformatteerd."
        };
      } else if (errorCode == "auth/wrong-password") {
        this.passwordError = {
          occured: true,
          message:
            "Het wachtwoord is incorrect of er is geen gebruiker met dit email adres."
        };
      }
    },
    clearErrors: function() {
      this.logInEmailError.occured = false;
      this.passwordError.occured = false;
    }
  }
};
</script>

<style>
</style>



