<template>
  <div id="app">
    <navbar></navbar>
    <main>
      <loading-spinner></loading-spinner>
      <router-view />
    </main>

    <modal-container />
  </div>
</template>

<script>
import Navbar from "./components/Navbar.vue";
import ModalContainer from "./components/modal-components/modal-container.vue";
import LoadingSpinner from "./components/LoaderSpinner.vue";
import firebase from "firebase";

export default {
  name: "App",
  components: {
    Navbar,
    ModalContainer,
    LoadingSpinner
  },
  methods: {},
  created: function() {
    firebase.auth().onAuthStateChanged(user => {
      this.$store.dispatch("setUser");
    });
  }
};
</script>

<style lang="scss">
@import "/scss/main.scss";
</style>



