<template>
  <div class="acousticsscore-chart">
    <canvas id="acousticsscoreChartCanvas" height="250"></canvas>
  </div>
</template>

<script>
export default {
  props: ["data", "height", "updateOnValueChange"],
  data: function() {
    return {
      acousticsscoreChart: ""
    };
  },
  watch: {
    updateOnValueChange: function() {
      this.acousticsscoreChart.data.datasets[0].data = [this.data.toFixed(0)];
      this.acousticsscoreChart.update();
    }
  },
  mounted: function() {
    const acousticsscoreChartCanvas = document.querySelector(
      "#acousticsscoreChartCanvas"
    );
    this.acousticsscoreChart = new Chart(acousticsscoreChartCanvas, {
      type: "radialGauge",
      responsive: true,
      data: {
        label: "Score",
        datasets: [
          {
            data: [this.data.toFixed(0)],
            backgroundColor: "#9fe2ff",
            borderColor: "#009ad9"
          }
        ]
      },
      options: {
        centerPercentage: 70,
        rotation: -Math.PI / 2,
        roundedCorners: false,
        trackColor: "#e9e9e9",
        centerArea: {
          fontFamily: "Inter UI",
          fontColor: "#009ad9"
        }
      }
    });
  }
};
</script>


